import React, { useEffect, useState } from 'react';
import './inspection-page.css';
import { useParams } from 'react-router';
import { httpFetch } from '../../../../services/http-fetch';
import { baseUrl } from '../../../../services/base-url';
import { dateToLocalTime } from '../../../../utils/date';
import { Inspection } from '../../../../types/inspection';
import checkupResultReplacer from '../../../../utils/CheckupResultReplacer';
import checkupResultTypeReplacer from '../../../../utils/сheckupResultTypeReplacer';
import nonAdmissionReasonsReplacer from '../../../../utils/nonAdmissionReasonsReplacer';
import { useHistory } from 'react-router-dom';
import { FetchData } from '../../../../gateways/http-fetch/http-fetch-response';
import Card from '../../common/card/card';
import NavigationHeader from '../../common/navigation-header/navigation-header';

type InspectionId = {
  id: string;
};

const InspectionPage = () => {
  const { id }: InspectionId = useParams();
  const history = useHistory();
  const [checkup, setCheckup] = useState<FetchData>({ attributes: {} });

  useEffect(() => {
    httpFetch.get(`${baseUrl}/checkups/${id}`).then(async data => {
      if (data) {
        await setCheckup(data.data);
      } else {
        history.push('/medicallist');
      }
    });
  }, []);

  const {
    user,
    media,
    arterial_pressure,
    alcohol,
    comment,
    complaints,
    medic,
    venous_pressure,
    datetime_end,
    datetime_start,
    result_datetime,
    result_auto,
    result_medic,
    graph_test,
    non_admission_reasons,
    pulse,
    schulte,
    temperature,
    type,
    terminal_name,
    weight,
  }: Inspection = checkup.attributes ? checkup.attributes : {};
  return (
    <div className="inspection">
      {checkup && Object.keys(checkup.attributes).length !== 0 && (
        <>
          <NavigationHeader>
            Медосмотр <b>№{id}</b>
          </NavigationHeader>

          <Card className="field__med" label="Имя" color="white">
            {user &&
              `${user.first_name} ${user.last_name && user.last_name[0]}. ${
                user.middle_name && user.middle_name[0]
              }.`}
          </Card>

          <Card className="field__med inspection_file" label="Фото пользователя" color="white">
            <img src={media?.user} alt="Пользователь" />
          </Card>

          <Card className="field__med" label="Тип осмотра" color="white">
            {checkupResultTypeReplacer(type)}
          </Card>

          <Card className="field__med" label="Время начала осмотра" color="white">
            {dateToLocalTime(datetime_start)}
          </Card>
          <Card className="field__med" label="Время окончания осмотра" color="white">
            {dateToLocalTime(datetime_end)}
          </Card>
          <Card className="field__med" label="Время заключения медика" color="white">
            {dateToLocalTime(result_datetime)}
          </Card>

          <Card className="field__med" label="Медик" color="white">
            {medic &&
              `${medic?.last_name} ${medic.first_name && medic.first_name[0]}. ${
                medic?.middle_name && medic.middle_name[0]
              }.`}
          </Card>

          <Card className="field__med" label="Заключение медика" color="white">
            {checkupResultReplacer(result_medic)}
          </Card>
          <Card className="field__med" label="Автоматический результат" color="white">
            {checkupResultReplacer(result_auto)}
          </Card>
          <Card className="field__med" label="Причины недопуска" color="white">
            {non_admission_reasons &&
              non_admission_reasons.length > 0 &&
              nonAdmissionReasonsReplacer(non_admission_reasons)}
          </Card>

          <Card className="field__med" label="Артериальное давление" color="white">
            {arterial_pressure}
          </Card>
          <Card className="field__med" label="Венозное давление" color="white">
            {venous_pressure}
          </Card>
          <Card className="field__med" label="Пульс" color="white">
            {pulse}
          </Card>
          <Card className="field__med" label="Результат измерения алкоголя" color="white">
            {alcohol ? alcohol : '0'}
          </Card>
          <Card className="field__med" label="Температура (℃)" color="white">
            {temperature}
          </Card>
          <Card className="field__med" label="Вес (кг)" color="white">
            {weight}
          </Card>
          <Card className="field__med" label="Количество ошибок в граф. тесте" color="white">
            {graph_test}
          </Card>
          <Card className="field__med" label="Время прохождения теста" color="white">
            {schulte}
          </Card>
          <Card className="field__med" label="Комментарий в случае недопуска" color="white">
            {comment}
          </Card>
          <Card className="field__med" label="Имя терминала" color="white">
            {terminal_name}
          </Card>
          <Card className="field__med" label="Жалобы" color="white">
            {complaints?.text}
          </Card>

          {media?.uvideo && (
            <Card className="field__med inspection_file" label="Видеозапись осмотра" color="white">
              <video src={media?.uvideo} controls />
            </Card>
          )}

          {media?.uvideo_profile && (
            <Card
              className="field__med inspection_file"
              label="Видеозапись осмотра в профиль"
              color="white"
            >
              <video controls>
                <source src={media?.uvideo_profile} />
              </video>
            </Card>
          )}

          {media?.uvideo_pupill && (
            <Card className="field__med inspection_file" label="Видеозапись зрачка" color="white">
              <video src={media?.uvideo_pupill} controls />
            </Card>
          )}

          {media?.uphoto && (
            <Card
              className="field__med inspection_file"
              label="Фото пользователя во время осмотра"
              color="white"
            >
              <img src={media?.uphoto} alt="Фото пользователя во время осмотра" />
            </Card>
          )}

          {media?.ucardio && (
            <Card className="field__med inspection_file" label="Кардиограмма" color="white">
              <img src={media?.ucardio} alt="Кардиограмма" />
            </Card>
          )}

          {media?.mouth && (
            <Card className="field__med inspection_file" label="Фотография рта" color="white">
              <img src={media?.mouth} alt="Фотография рта" />
            </Card>
          )}

          {media?.eyes && (
            <Card className="field__med inspection_file" label="Фотография глаз" color="white">
              <img src={media?.eyes} alt="Фотография глаз" />
            </Card>
          )}

          {media?.elbow && (
            <Card className="field__med inspection_file" label="Фотография локтя" color="white">
              <img src={media?.elbow} alt="Фотография локтя" />
            </Card>
          )}

          {media?.sign && (
            <Card className="field__med inspection_file" label="Подпись" color="white">
              <img src={media?.sign} alt="Подпись" />
            </Card>
          )}
        </>
      )}
    </div>
  );
};

export default InspectionPage;
