const nonAdmissionReasonsReplacer = (value?: number[]) => {
  return value?.map(item => {
    switch (true) {
      case item === 1:
        return ' на видео нет измерения алкоголя';
      case item === 2:
        return ' на видео нет измерения давления';
      case item === 3:
        return ' на видео нет измерения температуры';
      case item === 4:
        return ' на видео недостаточное освещение';
      case item === 5:
        return ' на видео другой водитель';
      case item === 6:
        return ' плохое качество видео';
      case item === 7:
        return ' алкоголь за пределами нормы';
      case item === 8:
        return ' давление за пределами нормы';
      case item === 9:
        return ' пульс за пределами нормы';
      case item === 10:
        return ' температура за пределами нормы';
      case item === 11:
        return ' несоответствие индивидуальным параметрам, не используется';
      case item === 12:
        return ' неправильно надета манжета';
      case item === 13:
        return ' есть жалобы';
    }
  });
};

export default nonAdmissionReasonsReplacer;
