const checkupResultTypeReplacer = (value?: number) => {
  switch (true) {
    case value === 0:
      return 'Предрейсовый';
    case value === 1:
      return 'Послерейсовый';
    case value === 2:
      return 'Предсменный';
    case value === 3:
      return 'Послесменный';
    case value === 4:
      return 'Предвахтовый';
    case value === 5:
      return 'Послевахтовый';
    case value === 6:
      return 'Автоматический';
  }
};

export default checkupResultTypeReplacer;
