const checkupResultReplacer = (value?: number) => {
  switch (true) {
    case value === 0:
      return 'Не допущен';
    case value === 1:
      return 'Допущен';
    case value === 2:
      return 'Ожидание заключения';
    case value === 3:
      return 'Требуется повторный осмотр';
  }
};

export default checkupResultReplacer;
